#clockContainer {
  height: 40vw;
  width: 40vw;
  background: url("https://media.geeksforgeeks.org/wp-content/uploads/20210302161254/imgonlinecomuaCompressToSizeOmNATjUMFKw-300x300.jpg") 0 0 / 100% no-repeat;
  margin: auto;
  position: relative;
}

#hour, #minute, #second {
  transform-origin: bottom;
  background: #000;
  border-radius: 10px;
  position: absolute;
}

#hour {
  width: 1.8%;
  height: 25%;
  opacity: .8;
  top: 25%;
  left: 48.85%;
}

#minute {
  width: 1.6%;
  height: 30%;
  opacity: .8;
  top: 19%;
  left: 48.9%;
}

#second {
  width: 1%;
  height: 40%;
  opacity: .8;
  top: 9%;
  left: 49.25%;
}

